import React from 'react';
import { removeHyphensAndCapitalize } from '../../utils/helpers';

function Portfolio({ image, name, deploy, link }) {
    return (
        <div className='portfolio' key={name}>


            <div style={{ backgroundImage: `url(${image})` }} className="bgImage" />
            <div className="portfolio-box-caption">
                <div className="portfolio-box-caption-content">
                    <a href={deploy} className="project-category text-faded" target="_blank">
                        <i className="fab fa-github"></i>
                    </a>
                    <br />
                    <a href={link} className="project-category text-faded" target="_blank">{removeHyphensAndCapitalize(name)}</a>{' '}
                </div>

            </div>
        </div>


    );
}

export default Portfolio;